@import "https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Poppins:wght@400;700&display=swap";
body {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.video-bg {
  height: 100vh;
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.video-bg video {
  z-index: 1;
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.effects {
  object-fit: cover;
  z-index: 2;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, .4);
  position: absolute;
  top: 0;
  left: 0;
}

.video-bg__content {
  z-index: 2;
  color: #fff;
  font-size: 50px;
}

.main {
  max-width: 1600px;
  margin: 50px auto 0;
  padding: 0 20px;
}

.search-form {
  width: 400px;
  margin: 10px auto;
  display: flex;
  position: relative;
}

.search-form input {
  height: 40px;
  border: 3px solid #253469;
  border-radius: 22px;
  flex-grow: 1;
  padding-left: 30px;
  font-size: 18px;
}

.search-form button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  height: 100%;
  background-color: #12182d;
  border: none;
  border-top-right-radius: 22px;
  border-bottom-right-radius: 22px;
  padding: 10px;
  font-size: 14px;
  position: absolute;
  top: 0;
  right: 0;
}

